<template>
    <div>
      <el-table
        :data="tableData"
        @row-click="tableClick"
        border
        :row-class-name="tableRowClassName"
        :class-name="tableShow"
        style="width: 100%;background: #000">

        <!---->
        <el-table-column
          fixed
          prop="date"
          label=""
          width=""
          sortable
          :filters="filts"
          :filter-method="filterHandler"
        >
        </el-table-column>
        <el-table-column
          prop="name"
          label=""
          width="">
        </el-table-column>
        <el-table-column
          prop="province"
          label=""
          width="">
        </el-table-column>
        <el-table-column
          prop="city"
          label=""
          width="">
        </el-table-column>
        <el-table-column
          prop="address"
          label=""
          width="">
        </el-table-column>
        <el-table-column
          prop="zip"
          label=""
          width="">
        </el-table-column>
        <el-table-column
          fixed="right"
          label=""
          width="">
          <template slot-scope="scope">
            <el-button  @click="handleClick(scope.row,$event)" type="text"></el-button>
          </template>
        </el-table-column>
      </el-table>


        <!---->
        <el-dialog
          title=""
          :visible.sync="centerDialogVisible"
          width="50%"
          >
          <el-form ref="form" :model="form" label-width="80px">

              <el-col :span="12">
                <el-form-item label="">
                <el-input v-model="form.name"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="">
                  <el-select v-model="form.region" placeholder="">
                    <el-option label="" value="shanghai"></el-option>
                    <el-option label="" value="beijing"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="">
                  <!--<el-input v-model="form.date"></el-input>-->
                    <el-date-picker type="date" placeholder="" v-model="form.date" style="width: 100%;"></el-date-picker>
                </el-form-item>
              </el-col>
              <el-col :span="12"><el-form-item label="">
                <el-input v-model="form.province"></el-input>
              </el-form-item>
              </el-col>
              <el-col :span="12"><el-form-item label="">
                <el-input v-model="form.city"></el-input>
              </el-form-item>
              </el-col>
              <el-col :span="12"><el-form-item label="">
                <el-input v-model="form.address"></el-input>
              </el-form-item>
              </el-col>
                <el-col :span="12"><el-form-item label="">
                  <el-input v-model="form.zip"></el-input>
                </el-form-item>
              </el-col>
          </el-form>

                <el-button right @click="centerDialogVisible = false"> </el-button>
                <el-button type="primary" :loading="loading" @click="onSubmit(form)"> </el-button>

        </el-dialog>
    <p>{{msg}}<el-button type="primary"  @click="home()">B</el-button></p>
      <el-button type="primary"  @click="clickTree()">ztree</el-button>
      <el-button type="primary"  @click="clickNotes()"></el-button>


    </div>

</template>

<script>
    export default {
        name: "hoursers",
        data(){
          return{
            msg:"A",
            tableShow:true,
            tableData: [{
              date: '2016-05-03',
              name: '',
              province: '',
              city: '',
              address: ' 1518 ',
              zip: 200333,
              region: '',
              test:1
            }, {
              date: '2016-05-02',
              name: '',
              province: '',
              city: '',
              address: ' 1518 ',
              zip: 200333,
              region: '',
              test:1
            }, {
              date: '2016-05-04',
              name: '',
              province: '',
              city: '',
              address: ' 1518 ',
              zip: 200333,
              region: '',
              test:1
            }, {
              date: '2016-05-01',
              name: '',
              province: '',
              city: '',
              address: ' 1518 ',
              zip: 200333,
              region: '',
              test:1
            }],
            filts:[

            ],
            centerDialogVisible:false,
            loading:false,
            form:{
            }
          }
        },
      methods: {
        home() {
          this.$router.push("/view/home/housers/hosersCode");
        },
        clickNotes(){
          this.$router.push("/view/home/housers/notes");
        },
        //
        handleClick(row,event) {
          event.cancelBubble = true;
          this.centerDialogVisible=true;
          this.form = { //elenmet row
            date: row.date,
            name: row.name,
            province:row.province,
            city: row.city,
            address:row.address,
            zip:row.zip,
          };
        },
        //
        onSubmit(form) {//row
          this.loading=true;
          setTimeout(()=>{
            this.loading=false;
            this.centerDialogVisible=false;
            this.$notify({
              title: '',
              message: '',
              duration:1000,
              type: 'success'
            });
          },3000)
          console.log(form);
        },
        clickTree(){
          this.tableData.forEach((item)=>{
            if(item.name =="" ){
              item.test+=1;
            }
          })
        },
        //
        editorClick(row,event) {
          event.cancelBubble = true;
        },
        //
        tableClick(row, event, column) {
          row.test+=1;
        },
        tableRowClassName({row}) {
          if (row.test%2==0) {
            return 'warning-row';
          } else if (row.test%2!=0) {
            return 'success-row';
          }
          return '';
        },
        tableTd(row){
          if (row.test%2==0) {
            return 'warning-row';
          } else if (row.test%2!=0) {
            return 'success-row';
          }
          return '';
        },
        //
        filtsData(){
          this.tableData.forEach((item)=>{
            let itemFilts={
              text:item.date,
              value:item.date
            }
            this.filts.push(itemFilts);
          })
        },
        //
        formatter(row, column) {
          return row.address;
        },
        filterTag(value, row) {
          return row.tag === value;
        },
        filterHandler(value, row, column) {
          const property = column['property'];
          return row[property] === value;
        }
      },
      mounted(){
        this.filtsData();
      }
    }
</script>

<style scoped>

</style>



