<template>
  <div>
  <div id="areaTree">
    <div class="box-title">
      <a href="#"><i class="fa  fa-refresh" @click="freshArea"></i></a>
    </div>
    <div class="tree-box">
      <div class="zTreeDemoBackground left">
        <ul id="treeDemo" class="ztree"></ul>
      </div>
    </div>
  </div>
  <p><el-button type="primary"  @click="home()">B</el-button></p>
  </div>
</template>

<script>
  export default {
    name: 'areaTree',
    components:{

    },
    data(){
      return{
        setting:{
          edit: {
            enable: true,
            drag: {
              isCopy: true,
              isMove: false,
              prev:true,
              inner:true,
              next:true
            }
          }
        },
        zNodes:[
          { name:"1 - ", open:true,
            children: [
              { name:"11 - ",
                children: [
                  { name:"111"},
                  { name:"112"},
                  { name:"113"},
                  { name:"114"}
                ]},
              { name:"12 - ",
                children: [
                  { name:"121"},
                  { name:"122"},
                  { name:"123"},
                  { name:"124"}
                ]},
              { name:"13 - ", isParent:true}
            ]},
          { name:"2 - ",
            children: [
              { name:"21 - ", open:true,
                children: [
                  { name:"211"},
                  { name:"212"},
                  { name:"213"},
                  { name:"214"}
                ]},
              { name:"22 - ",
                children: [
                  { name:"221"},
                  { name:"222"},
                  { name:"223"},
                  { name:"224"}
                ]},
              { name:"23 - ",
                children: [
                  { name:"231"},
                  { name:"232"},
                  { name:"233"},
                  { name:"234"}
                ]}
            ]},
          { name:"3 - ", isParent:true}

        ]
      }
    },
    methods:{
      home() {
        this.$router.push("/view/home/info");
      },
    },
    mounted(){
      $.fn.zTree.init($("#treeDemo"), this.setting, this.zNodes);
    }
  }
</script>
<style>
  #areaTree{
    border:1px solid #e5e5e5;    margin-bottom: 2px;border-radius: 4px;overflow: hidden;
  }
  .box-title{
    border-radius: 3px 3px 0 0;background-color: #f5f5f5;
  }
  .box-title a{
    color: #2fa4e7;
    text-decoration: none;font-size:14px;    display: block;
    padding: 8px 15px;cursor: pointer;
  }
  .box-title .fa{
    float:right;line-height: 20px;
  }
</style>



